<template>
    <el-table :data="tableData" stripe style="">
         <el-table-column prop="userId" label="用户id" width="120"></el-table-column>
         <el-table-column prop="busName" label="业务" width="100"></el-table-column>
         <el-table-column prop="busId" label="信用分业务id" width="180"></el-table-column>
         <el-table-column prop="score" label="分数" width="100"></el-table-column>
         <el-table-column prop="adjustTime" label="分数调整时间" width="210"></el-table-column>
         <el-table-column label="加分有效期" width="210">

             <template scope="ff">
                  <span style="margin-left: 10px">{{ ff.row.validStartTime?(ff.row.validStartTime+"至"+ff.row.validEndTime):"" }}</span>
                </template>
         </el-table-column>
         <el-table-column prop="status" label="当前状态" width="100"></el-table-column>
         <el-table-column prop="memo" label="备注"></el-table-column>
         <el-table-column prop="operator" label="操作账号" width="100"></el-table-column>
  </el-table>
</template>

<script>

import Vue from "vue";
export default {
    props:{
        tableData:{
             default:[]
        }
    }
}
</script>



